.header {
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 20px;
    height: 60px;
    border-bottom: 4px solid #3F51B5;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}
.header__logo {
    text-decoration: none;
    color: #3F51B5;
}
.navbar__list {
    display: inline-block;
    margin-left: 20px;
    list-style-type: none;

}
.navbar__list>a {
    text-decoration: none;
    color: #424242;
    font-size: 25px;

}
.navbar__list>a:hover {
    color: #9E9E9E;
}



